
import Card from "./components/Card"
import axios from 'axios';
import { useEffect, useState } from "react";
import styles from './ShopContent.module.css'
import Header from "../Header/Header";
import NotFound from "../NotFound/NotFound";
import { Route, Routes } from "react-router";
import Details from "../Details/Details";
import AppContext from "./context";
import LoginPage from "./LoginPage";
import RegisterPage from "./RegisterPage";
import FavoritePage from "../Favorite/Favorite";


const ShopContent = () => {
    const [items, setItems] = useState([]); // Основные карточки
    const [cartItems, setCartItems] = useState([]); // Карточки в корзине
    const [favorites, setFavorites] = useState([]); // Карточки в фаворит
    const [searchInput, setUsername] = useState(''); // Поиск

    const [currentPage, setCurrentPage] = useState(1);
    const [cartPerPage] = useState(12);

    const lastCartIndex = currentPage * cartPerPage;
    const firstCartIndex = lastCartIndex - cartPerPage;
    const currentCart = items.slice(firstCartIndex, lastCartIndex);
    
    const pageNumbers = [];

    for(let i = 1; i <= Math.ceil(items.length / cartPerPage); i++) {
      pageNumbers.push(i);
    }

    const paginate = pageNumber => setCurrentPage(pageNumber);
    const nextPage = () => setCurrentPage(prev => prev + 1);

    useEffect(() => {
        async function fetchData() {
          try {
            const [cartResponse, favoritesResponse, itemsResponse] = await Promise.all([
              axios.get('https://617aac64cb1efe001700ffa7.mockapi.io/items'),
              axios.get('https://617aac64cb1efe001700ffa7.mockapi.io/favorites'),
              axios.get('https://617aac64cb1efe001700ffa7.mockapi.io/card'),
            ]);

            setCartItems(cartResponse.data);
            setFavorites(favoritesResponse.data);
            setItems(itemsResponse.data);
          } catch (error) {
            alert('Ошибка при запросе данных ;(');
            console.error(error);
          }
        }
    
        fetchData();
    }, []);

    const onAddToCart = async (obj) => {
        try {
          const findItem = cartItems.find((item) => Number(item.parentId) === Number(obj.id));
          if (findItem) {
            setCartItems((prev) => prev.filter((item) => Number(item.parentId) !== Number(obj.id)));
            await axios.delete(`https://617aac64cb1efe001700ffa7.mockapi.io/items/${findItem.id}`);
          } else {
            setCartItems((prev) => [...prev, obj]);
            const { data } = await axios.post('https://617aac64cb1efe001700ffa7.mockapi.io/items', obj);
            setCartItems((prev) =>
              prev.map((item) => {
                if (item.parentId === data.parentId) {
                  return {
                    ...item,
                    id: data.id,
                  };
                }
                return item;
              }),
            );
          }
        } catch (error) {
          alert('Ошибка при добавлении в корзину');
          console.error(error);
        }
    };

    const onAddToFavorite = async (obj) => {
      try {
        if (favorites.find((favObj) => Number(favObj.id) === Number(obj.id))) {
          axios.delete(`https://617aac64cb1efe001700ffa7.mockapi.io/favorites/${obj.id}`);
          setFavorites((prev) => prev.filter((item) => Number(item.id) !== Number(obj.id)));
        } else {
          const { data } = await axios.post(
            'https://617aac64cb1efe001700ffa7.mockapi.io/favorites',
            obj,
          );
          setFavorites((prev) => [...prev, data]);
        }
      } catch (error) {
        alert('Не удалось добавить в фавориты');
        console.error(error);
      }
    };
  
    const onRemoveItem = (id) => {
        try {
            axios.delete(`https://617aac64cb1efe001700ffa7.mockapi.io/items/${id}`);
            setCartItems((prev) => prev.filter((item) => Number(item.id) !== Number(id)));
        } catch (error) {
            alert('Ошибка при удалении из корзины');
            console.error(error);
        }
    };

    const isItemAdded = (id) => {
        return cartItems.some((obj) => Number(obj.parentId) === Number(id));
    };

    const shopPosts = currentCart
    .filter((currentCart) => currentCart.title.toLowerCase().includes(searchInput.toLowerCase()))
    .map((currentCart) => {

        return (
            <Card 
                key={currentCart.id}
                image={currentCart.image}
                title={currentCart.title}
                testimonials={currentCart.testimonials}
                available={currentCart.available}
                cost={currentCart.cost}
                discount={currentCart.discount}
                procentdiscount={currentCart.procentdiscount}
                onAdd={(obj) => onAddToCart(obj)}
                onFavorite={(obj) => onAddToFavorite(obj)}
                
                {...currentCart}
            />
        )
    })

        return (
          <>
          <AppContext.Provider
            value={{
                items,
                cartItems,
                isItemAdded,
                onAddToCart,
                setCartItems, 
                onAddToFavorite,
                favorites
            }}>
            <Header 
            items={cartItems} 
            onRemove={onRemoveItem}
            onSearch={setUsername}
            />
            <div className="container">
            <Routes basename="/react-deploy"> 
                <Route exact path="/" element={
                    <div className={styles.grid_contain}>
                      <div className={styles.filters}>
                          <div className={styles.rr}>
                              <h3 className={styles.filters_price__title}>Цена</h3>
                              <input type="text"></input>
                          </div>
                      </div>
                      <div className={styles.main__container}>
                        <div className={styles.banner__container}>
                          <div className={styles.banner__item}>
                            <h3>Телефоны</h3>
                            <img src="https://icenter.ua/image/cache/catalog/image/cache/catalog/iph-11-440x365.webp" alt="lorem ipsum"></img>
                          </div>
                          <div className={styles.banner__item}>
                            <h3>Про телефоны</h3>
                            <img src="https://icenter.ua/image/cache/catalog/image/cache/catalog/iph-xr-440x365.webp" alt="lorem ipsum"></img>
                          </div>
                          <div className={styles.banner__item}>
                            <h3>Про телефоны</h3>
                            <img src="https://icenter.ua/image/cache/catalog/image/cache/catalog/iph-xr-440x365.webp" alt="lorem ipsum"></img>
                          </div>
                        </div>
                        <div className={styles.module__header}>
                          <h3>Популярные товары</h3>
                        </div>
                        <div className={styles.products_grid}>
                            {shopPosts.length > 0 ? shopPosts 
                            :             
                            <div className="centerFixed">
                                <div className="contentFix">
                                    <div className="pswp__preloader__icn">
                                        <div className="pswp__preloader__cut">
                                            <div className="pswp__preloader__donut"></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            }
                        </div>
                        <div className={styles.pagination__container}>
                          {
                            pageNumbers.map(number => (
                              <span key={number} onClick={() => paginate(number)}>{number}</span>
                            ))
                          }
                          {
                            pageNumbers.length === 1 ? 
                            null : 
                            <>
                              <span>...</span>
                              <h3 className={styles.pagination__next} onClick={nextPage}>Следующая</h3>
                            </>
                          }
                              
                        </div>
                      </div>
                    </div>
                }/>
                <Route exact path="/products/:id" element={<Details 
                    onAdd={(obj) => onAddToCart(obj)}
                />}/>
                <Route exact path="*" element={<NotFound />}/>
                <Route exact path="/login" element={<LoginPage />}/>
                <Route exact path="/register" element={<RegisterPage />}/>
                <Route exact path="/favorite" element={
                  <div className={styles.products_grid} style={{margin: "25px 0px"}}>
                    {shopPosts.length > 0 ? <FavoritePage onAddToFavorite={onAddToFavorite} onFavorite={(obj) => onAddToFavorite(obj)}/> 
                    :             
                    <div className="centerFixed">
                        <div className="contentFix">
                            <div className="pswp__preloader__icn">
                                <div className="pswp__preloader__cut">
                                    <div className="pswp__preloader__donut"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    }
                  </div> 
                }/>
            </Routes> 
            </div>
          </AppContext.Provider>
          </>
        )
    }

export default ShopContent;